import Ball from './components/ball/ball.jsx'
import Tab from './components/tab/tab.jsx'
import Lottery from './lottery'
import './App.css';

// 图片
import trashIcon from './images/trash.png'


function App() {
  const games = [new Lottery('大乐透',36),new Lottery('双色球',35)];

  
  

//数字彩tab的渲染函数
  const gameItem = games.map(game=>{
    return<Tab name={game.name} />
  })

//数字彩号码选择渲染函数
  const renderBalls = (game)=>{
   
    let ballList = [];
    let maxNum = 1
    while(maxNum < game.maxNum){
     
      ballList.push(<Ball num={maxNum} />)
      maxNum = maxNum+1
    }
    
   return ballList;
  }

  return (
    <div className="App">
      <header class='tab-bar'>{gameItem}</header>
      
      <div class ='selection-area'>
      <div class='balls' >
      {renderBalls(games[0])}
      </div>
      </div>
    
    <footer class='footer' >
      <img src={trashIcon} alt='delete' ></img>
      {/* <div class='divider' /> */}
      <div>共2注, 供4元</div>
      <img src={trashIcon} alt='delete' ></img>
    </footer>
    </div>
  );
}

export default App;
